import React, { useEffect } from 'react';
import { injectIntl } from 'react-intl';
import { Menu, Icon } from 'antd';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const MenuItem = styled(Menu.Item)`
  cursor: pointer;
`;

const { SubMenu } = Menu;
const FLOWIMAGE = `${process.env.PUBLIC_URL}/images/hierarchy.png`;

const Menus = ({ user, rol, pathname, intl: { messages } }) => {
  useEffect(() => {
    console.log(pathname);
  }, [pathname]);
  return (
    <React.Fragment>
      {rol === 'user' && (
        <Menu defaultSelectedKeys={[pathname]} mode="inline">
          <MenuItem key="/dashboard/process-services/new">
            <Link to="/dashboard/process-services/new">
              <Icon type="file-add" />
              <span> {messages.process.newProcess} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard">
            <Link to="/dashboard">
              <Icon type="dashboard" />
              <span> {messages.menus.dashboard} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard/process-services">
            <Link to="/dashboard/process-services">
              <Icon type="desktop" />
              <span> {messages.menus.processServices} </span>
            </Link>
          </MenuItem>

          <MenuItem key="/dashboard/stats">
            <Link to="/dashboard/stats">
              <Icon type="line-chart" />
              <span> {messages.menus.stats} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard/profile">
            <Link to="/dashboard/profile">
              <Icon type="profile" />
              <span> {messages.menus.profile} </span>
            </Link>
          </MenuItem>

          <Menu.Divider />
          {(user.canTrain || user.canClone) && (
            <SubMenu
              key="sub2"
              title={
                <span>
                  <Icon type="api" />
                  <span>Corporate</span>
                </span>
              }
            >
              <MenuItem key="/dashboard/models">
                <Link to="/dashboard/models">
                  <Icon type="block" />
                  <span> {messages.menus.models} </span>
                </Link>
              </MenuItem>
            </SubMenu>
          )}

          <Menu.Divider />
        </Menu>
      )}

      {rol === 'client' && (
        <Menu defaultSelectedKeys={[pathname]} mode="inline">
          <MenuItem key="/dashboard/process-services/new">
            <Link to="/dashboard/process-services/new">
              <Icon type="file-add" />
              <span> {messages.process.newProcess} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard">
            <Link to="/dashboard">
              <Icon type="dashboard" />
              <span> {messages.menus.dashboard} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard/process-services">
            <Link to="/dashboard/process-services">
              <Icon type="desktop" />
              <span> {messages.menus.processServices} </span>
            </Link>
          </MenuItem>

          <MenuItem key="/dashboard/profile">
            <Link to="/dashboard/profile">
              <Icon type="profile" />
              <span> {messages.menus.profile} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard/stats">
            <Link to="/dashboard/stats">
              <Icon type="line-chart" />
              <span> {messages.menus.stats} </span>
            </Link>
          </MenuItem>

          <Menu.Divider />
          <SubMenu
            key="sub1"
            title={
              <span>
                <Icon type="api" />
                <span>Corporate</span>
              </span>
            }
          >
            <MenuItem key="/dashboard/models">
              <Link to="/dashboard/models">
                <Icon type="block" />
                <span> {messages.menus.models} </span>
              </Link>
            </MenuItem>

            <MenuItem key="/dashboard/engines">
              <Link to="/dashboard/engines">
                <Icon type="tool" />
                <span> {messages.menus.engines} </span>
              </Link>
            </MenuItem>
          </SubMenu>
          <Menu.Divider />
          <SubMenu
            key="sub2"
            title={
              <span>
                <Icon type="build" />
                <span>WorkFlow</span>
              </span>
            }
          >
            <MenuItem key="/dashboard/projects">
              <Link to="/dashboard/projects">
                <Icon type="project" />
                <span> {messages.menus.projects} </span>
              </Link>
            </MenuItem>
            <MenuItem key="/dashboard/jobs">
              <Link to="/dashboard/jobs">
                <Icon type="robot" />
                <span> Jobs </span>
              </Link>
            </MenuItem>
            <MenuItem key="/dashboard/tasks">
              <Link to="/dashboard/tasks">
                <Icon type="ordered-list" />
                <span> Tasks </span>
              </Link>
            </MenuItem>
          </SubMenu>
          <Menu.Divider />
          <MenuItem key="/dashboard/users">
            <Link to="/dashboard/users">
              <Icon type="user" />
              <span> {messages.menus.user} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard/subscriptions">
            <Link to={`/dashboard/subscriptions/user/${user.id}`}>
              <Icon type="bell" />
              <span> {messages.menus.subscription} </span>
            </Link>
          </MenuItem>
        </Menu>
      )}

      {rol === 'admin' && (
        <Menu defaultSelectedKeys={[pathname]} mode="inline">
          <MenuItem key="/dashboard/process-services/new">
            <Link to="/dashboard/process-services/new">
              <Icon type="file-add" />
              <span> {messages.process.newProcess} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard">
            <Link to="/dashboard">
              <Icon type="dashboard" />
              <span> {messages.menus.dashboard} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard/process-services">
            <Link to="/dashboard/process-services">
              <Icon type="desktop" />
              <span> {messages.menus.processServices} </span>
            </Link>
          </MenuItem>

          <MenuItem key="/dashboard/stats">
            <Link to="/dashboard/stats">
              <Icon type="line-chart" />
              <span> {messages.menus.stats} </span>
            </Link>
          </MenuItem>
          <Menu.Divider />
          <SubMenu
            key="sub1"
            title={
              <span>
                <Icon type="api" />
                <span>Corporate</span>
              </span>
            }
          >
            <MenuItem key="/dashboard/nodes">
              <Link to="/dashboard/nodes">
                <Icon type="deployment-unit" />
                <span> {messages.menus.nodes} </span>
              </Link>
            </MenuItem>

            <MenuItem key="/dashboard/models">
              <Link to="/dashboard/models">
                <Icon type="block" />
                <span> {messages.menus.models} </span>
              </Link>
            </MenuItem>

            <MenuItem key="/dashboard/eds">
              <Link to="/dashboard/eds">
                <Icon type="apartment" />
                <span> EDS </span>
              </Link>
            </MenuItem>
            <MenuItem key="/dashboard/services">
              <Link to="/dashboard/services">
                <Icon type="control" />
                <span> {messages.menus.services} </span>
              </Link>
            </MenuItem>
            <MenuItem key="/dashboard/engines">
              <Link to="/dashboard/engines">
                <Icon type="tool" />
                <span> {messages.menus.engines} </span>
              </Link>
            </MenuItem>
          </SubMenu>
          <Menu.Divider />
          <SubMenu
            key="sub2"
            title={
              <span>
                <Icon type="build" />
                <span>WorkFlow</span>
              </span>
            }
          >
            <MenuItem key="/dashboard/actors">
              <Link to="/dashboard/actors">
                <Icon type="smile" />
                <span> {messages.menus.actors} </span>
              </Link>
            </MenuItem>

            <MenuItem key="/dashboard/projects">
              <Link to="/dashboard/projects">
                <Icon type="project" />
                <span> {messages.menus.projects} </span>
              </Link>
            </MenuItem>

            <MenuItem key="/dashboard/jobs">
              <Link to="/dashboard/jobs">
                <Icon type="robot" />
                <span> Jobs </span>
              </Link>
            </MenuItem>
            <MenuItem key="/dashboard/tasks">
              <Link to="/dashboard/tasks">
                <Icon type="ordered-list" />
                <span> Tasks </span>
              </Link>
            </MenuItem>
          </SubMenu>
          <Menu.Divider />
          <MenuItem key="/dashboard/users">
            <Link to="/dashboard/users">
              <Icon type="user" />
              <span> {messages.menus.user} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard/subscriptions">
            <Link to="/dashboard/subscriptions">
              <Icon type="bell" />
              <span> {messages.menus.subscription} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard/profile">
            <Link to="/dashboard/profile">
              <Icon type="profile" />
              <span> {messages.menus.profile} </span>
            </Link>
          </MenuItem>
          <MenuItem key="/dashboard/configuration">
            <Link to="/dashboard/configuration">
              <Icon type="setting" />
              <span> {messages.menus.setting} </span>
            </Link>
          </MenuItem>
        </Menu>
      )}
    </React.Fragment>
  );
};

export default injectIntl(Menus);
